/* src/pages/DataTable.module.css */

.container {
  padding: 20px;
}

.tableWrapper {
  overflow-x: auto;
  margin-bottom: 30px;
}

.table {
  width: 100%;
  border-collapse: collapse;
}

.table th,
.table td {
  border: 1px solid #ddd;
  padding: 12px;
  text-align: center;
}

.table th {
  background-color: #f2f2f2;
}

.deleteButton {
  padding: 6px 12px;
  background-color: #f44336; /* Red */
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.deleteButton:hover {
  background-color: #d32f2f;
}

.editButton {
  padding: 6px 12px;
  background-color: #2196f3; /* Blue */
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-right: 5px;
}

.editButton:hover {
  background-color: #1976d2;
}

.controlSignalsList {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.controlSignalsList li {
  padding: 4px 0;
  font-size: 12px;
}

.on {
  color: #4caf50; /* Green */
}

.off {
  color: #f44336; /* Red */
}

.error {
  color: #f44336;
}

/* Styles for Weather Table */
.weatherTable th,
.weatherTable td {
  border: 1px solid #ddd;
  padding: 12px;
  text-align: center;
}
